import React, { Component } from 'react';

class Createweeklyjournal extends Component {
       handleSubmit = event => {
         event.preventdefault();
         console.log("button clicked")
         console.log("topic name"+ event.target.topic.value)
         console.log("event1"+ event.target.question1.value)
         console.log("event2"+ event.target.question2.value)
         console.log("event3"+ event.target.question3.value)
       }
       constructor(props) {
        super(props);
        
        this.state = {
           data: ''
        }
        this.updateState = this.updateState.bind(this);
        this.newState = this.newState.bind(this);
     };
     updateState() {
        this.setState({data: <div className = "row">
        <div className = "col-sm-12">
          <div className = "card" style = {{backgroundColor:"white"}}>
          <form onSubmit={this.handleSubmit}>
          <table>
  <tr>
    <th>Topic</th>
    <th><input type = "text" name = "topic" placeholder = "name of the topic"/></th>
  </tr>
  <tr>
    <td>Question 1</td>
    <td><input type = "text" name = "question1" placeholder = "enter question"/></td>
  </tr>
  <tr>
    <td>Question 2</td>
    <td><input type = "text" name = "question2" placeholder = "enter question"/></td>
  </tr>
  <tr>
    <td>Question 3</td>
    <td><input type = "text" name = "question3" placeholder = "enter question"/></td>
  </tr>
  <button type = "submit" class = "btn btn-primary">Submit</button>
</table>
</form>
        </div>
        </div>
        </div>
    })
     }

     newState(){
       this.setState({data: <h1>Topic Saved</h1>})
     }
  render() {
    return  <div>
    <button type = "submit" className = "btn btn-success" onClick = {this.updateState} style = {{fontSize: "20px", width:"150px"}}>+</button>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
    <button type = "submit" className = "btn btn-success" onClick = {this.newState} style = {{fontSize: "20px", width:"150px"}}>SAVE</button>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
    <button type = "submit" className = "btn btn-success" style = {{fontSize: "20px", width:"150px"}}>submit</button>
    <h4>{this.state.data}</h4>
 </div>
  }
}

export default Createweeklyjournal;
