import React, { Component } from 'react';
import './viewwaterintake.css';
import axios from 'axios';
import LoadingOverlay from 'react-loading-overlay'
import {Breadcrumb, BreadcrumbItem} from 'reactstrap';
import Moment from 'moment'
class Viewwaterintake extends Component {

  constructor(props) {
    super(props);
    this.state = {
      waterintake1: [],
      user:[],
      isActive:true,
      id: "5d53e09f58ce1273bf02c6ae",
      date: "1566325800"
    }
  }
  componentDidMount() {
    var d = new Date();
    var date = Moment((d.getYear()+1900)+""+("0" + (d.getMonth() + 1)).slice(-2)+""+("0" + d.getDate()).slice(-2))/1000;
    axios
      .get('https://cors-anywhere.herokuapp.com/http://13.233.64.181:5000/api/getwaterintake/'+this.props.location.state.className+ '/'+this.props.location.state.usrid+ '/'+date)
      .then(res => this.setState({ waterintake1: res.data.data, viewwater: res.data }))
      .catch(err => console.log(err))
      .then((res) =>{
        if(this.state.viewwater.success === true){
          this.setState({
            isActive: false
          });
        }
      })

      axios
      .get('https://cors-anywhere.herokuapp.com/http://13.233.64.181:5000/api/getchild/'+this.props.location.state.className+'/'+this.props.location.state.usrid)
      .then(res => this.setState({ user: res.data.data }))
      .catch(err => console.log(err))
  }
  render() {
    // console.log(JSON.stringify(user) + "userssss");
    return (
      <div>
         <LoadingOverlay
  active={this.state.isActive}
  spinner
  text=''
  >
        <br/>
        <Breadcrumb>
        <BreadcrumbItem>Home </BreadcrumbItem>
        <BreadcrumbItem>Student list </BreadcrumbItem>
        <BreadcrumbItem active style={{ color: "#ffa901" }}>View Water Intake</BreadcrumbItem>
      </Breadcrumb>
        <div class = "col-sm-12"><br/>
        <img src = {this.state.user.Image} alt = "male img" height = "200px" className = "center"/><br/>
        <h1 style = {{textAlign: "center"}}> {this.state.user.first_name}&nbsp;{this.state.user.middle_name}&nbsp;{this.state.user.last_name}</h1>
        </div>&nbsp;
        <br/>
        <div class = "card" style = {{backgroundColor:"white"}}>
        <table style={{ marginTop: "50px" }}>
              <tr style ={{backgroundColor:"lightgrey"}}>
                <th>&nbsp; &nbsp;Water Timings</th>
                <th>&nbsp; &nbsp;Water Intake</th>
              </tr>
              {this.state.waterintake1.map(water => {
              return <tr>
                <td style={{ textAlign: "center" }}>
                  <h3 style={{ marginLeft: "10px" }}>{water.time}</h3>
                </td>
                <td style={{ textAlign: "center" }}>
                  <h3 style={{ marginLeft: "10px" }}>{water.waterIntake} ml</h3>
                </td>
              </tr>
            })}
            </table>
      </div>
      </LoadingOverlay>
      </div>
    )
  }
}

export default Viewwaterintake;
